@import url("https://fonts.googleapis.com/css?family=Roboto");

.Weather {
  margin: 0 auto 1em;
  width: 100%;
  transform: scale(1.1);
  font-family: "Roboto", sans-serif;
  font-size: 0.9vw;
  max-height: 400em;
  display: flex;
}

h3 {
  font-size: 1.7em;
  margin: 0 auto;
  margin-bottom: 0.3em;
}

p {
  margin-top: 0em;
  margin-bottom: 0.3em;
  margin-left: 0em;
  margin-right: 0em;
}

.Weather .container {
  text-align: center;
  width: 90%;
  margin: 0 auto;
  border-radius: 10px;
  overflow: hidden;
}

.Weather .column_one {
  display: inline-block;
  flex-direction: row;
  width: 45%;
  text-align: left;
  padding-left: 5px;
  font-size: 1.5em;
  text-shadow: 3px 3px 3px rgb(0, 0, 0, 0.4);
  position: absolute;
  bottom: 0;
  left: 30px;
}

.Weather .column_two {
  display: inline-block;
  flex-direction: row;
  width: 45%;
  text-align: right;
  padding-right: 5px;
  text-shadow: 3px 3px 3px rgb(0, 0, 0, 0.4);
  position: absolute;
  bottom: 14px;
  right: 30px;
}

.Weather .icon {
  margin: 0em;
  position: relative;
  top: -1.7rem;
  transform: scale(1.7);
  /* height: 100%; */
  width: 11em;
  /* margin: 0.05em; */
  margin-bottom: 2em;
  margin-top: 2em;
}

.Weather .temp {
  font-size: 2em;
}

.Weather .clearDay {
  /* background-color: #3dcbff; */
  background-image: url("https://images.freeimages.com/images/large-previews/d85/blue-sky-1396430.jpg");
  background-size: cover;
}

.Weather .clearNight {
  /* background-color: #2c4b89; */
  background-image: url("https://images.freeimages.com/images/large-previews/7c4/afternoon-moon-1496370.jpg");
}

