  background-size: cover;
}

.Weather .snow {
  /* background-color: #e0f8ff; */
  background-image: url("https://images.freeimages.com/images/large-previews/b87/snow-1395478.jpg");
  background-size: cover;
}

.Weather .sleet {
  /* background-color: #e2f5ff; */
  background-image: url("https://images.freeimages.com/images/large-previews/995/icy-briars-2-1248805.jpg");
  background-size: cover;
}

.Weather .wind {
  /* background-color: #2c4b89; */
  background-image: url("https://images.freeimages.com/images/large-previews/4d3/wind-farm-1214436.jpg");
  background-size: cover;
